import React from 'react';
import {
  Box, Button, IconCirclePlus,
} from '@aragon/ui';
import { advance } from '../../utils/web3';
import NumberBlock from "../common/NumberBlock";
import { EQDS } from "../../constants/tokens";

type AdvanceEpochProps = {
  user: string,
  epoch: number,
  epochTime: number,
}

function AdvanceEpoch({
  user,
  epoch,
  epochTime,
}: AdvanceEpochProps) {
  return (
    <div style={{ marginBottom: '2%', display: 'block', position: 'relative', background: '#ffffff' }}>
      <h2 style={{ padding: '2%' }} className="bg-warning">Advance Epoch</h2>
      <div className="bs-component" style={{ padding: '2%', color: '#000000' }}>
        <div style={{ display: 'flex' }}>
          {/* Epoch Time */}
          <div style={{ width: '30%' }}>
            <NumberBlock title="Epoch (from current time)" num={epochTime} />
          </div>
          {/* Advance Epoch */}
          <div style={{ width: '40%' }} />
          <div style={{ width: '30%', paddingTop: '2%' }}>
            <Button
              wide
              icon={<IconCirclePlus />}
              label="Advance"
              onClick={() => {
                advance(EQDS.addr);
              }}
              disabled={user === '' || epoch >= epochTime}
            />
          </div>
        </div>
      </div>
    </div>
  );
}


export default AdvanceEpoch;
