import React from 'react';
import BigNumber from "bignumber.js";
import { Box } from '@aragon/ui';
import styled from 'styled-components'

type InvestProps = {
  totalSupply: BigNumber,
  EQDLPBonded: BigNumber,
};

const Invest = ({ totalSupply, EQDLPBonded }: InvestProps) => {
  const EQDLpBonded = EQDLPBonded.toNumber() * 2;
  const lpHourly = ((((totalSupply.toNumber() * 4) / 100) * 40) / 100 + EQDLpBonded) / EQDLpBonded;
  const lpDaily = (((((totalSupply.toNumber() * 4) / 100) * 40) / 100) * 24 + EQDLpBonded) / EQDLpBonded;
  const lpWeekly = (((((totalSupply.toNumber() * 4) / 100) * 40) / 100) * 168 + EQDLpBonded) / EQDLpBonded;

  return (

    <Container>
      <ContainerItem style={{ flexBasis: '30%', flexGrow: 1, marginRight: '2%' }}>
        <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
          <ul className="list-group" style={{ background: '#000084' }}>
            <li className="d-flex justify-content-between align-items-center" style={{ padding: '5%', height: '230px' }}>
              <div>
                <div style={{ fontSize: 16, padding: 3 }}>APR</div>
                <div style={{ fontSize: 16, padding: 3 }}>LP hourly:
              <div style={{
                    fontSize: 24,
                    padding: 3,
                    fontWeight: 400,
                    lineHeight: 1.5,
                    fontFamily: 'DOS, monospace'
                  }}>
                    {((lpHourly - 1) * 100).toFixed(2)}%
              </div>
                </div>
                <div style={{ fontSize: 16, padding: 3 }}>LP daily:
              <div style={{
                    fontSize: 24,
                    padding: 3,
                    fontWeight: 400,
                    lineHeight: 1.5,
                    fontFamily: 'DOS, monospace'
                  }}>
                    {((lpDaily - 1) * 100).toFixed(2)}%
              </div>
                </div>
                <div style={{ fontSize: 16, padding: 3 }}>LP weekly:
              <div style={{
                    fontSize: 24,
                    padding: 3,
                    fontWeight: 400,
                    lineHeight: 1.5,
                    fontFamily: 'DOS, monospace'
                  }}>
                    {((lpWeekly - 1) * 100).toFixed(2)}%
              </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </ContainerItem>
      <ContainerItem style={{ flexBasis: '68%' }}>
        <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
          <ul className="list-group" style={{ background: '#000084', padding: '4%', height: '250px' }}>
            <label style={{ fontSize: 26 }}><strong>Guide for LP bonding</strong></label>

            <p className="mt-4">Step 1: You need to have CAKE-LP in your wallet (by adding Liquidity to BUSD-EQD pair).</p>

            <p className="mt-2">Step 2: Approve your CAKE-LP to unlock Staging (skip it if you've done it)</p>

            <p className="mt-2">Step 3: Stage your CAKE-LP into LP</p>

            <p className="mt-2">Step 4: Bond your CAKE-LP into LP</p>

            <p className="mt-2">Step 5: Unbond and wait 10 hours to claim your rewards</p>
          </ul>
        </div>
      </ContainerItem>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 1%;
  @media (max-width: 568px) {
    display: block;
  }
`

const ContainerItem = styled.div`
  @media (max-width: 568px) {
    margin: 2% !important;
  }
`

export default Invest;