import React from 'react';
import styled from 'styled-components'

function About() {
  return <Container>
    <div style={{ marginBottom: '2%', display: 'block', position: 'relative', background: '#ffffff' }}>
      <div className="bs-component" style={{ padding: '2%', color: '#000000' }}>
        <Title>Introducing Equilibrium Dollar Protocol</Title>
        <Text>Equilibrium Dollar Protocol is a decentralized, oracle data driven stablecoin which uses a supply elasticity
        method around a Time Weighted Average Price (TWAP) oracle for price stability. Its an algorithmic stablecoin
      which uses TWAP to stabilize the price. Our goal is to maintain the price $EQD = 1 USD in long term.</Text>
        <Logo
          src="./logo/logo_center.png"
          alt="Equilibrium Dollar Protocol"
        />
        <TextSub>Logo Equilibrium Dollar Protocol</TextSub>
        <div>
          <Text>
            Token: <a
              href="https://bscscan.com/token/0x5Aae642C7ceA19A027590ea42f2366f64377c06F"
              target="_blank"
              rel="noopener noreferrer"
            >https://bscscan.com/token/0x5Aae642C7ceA19A027590ea42f2366f64377c06F</a>
          </Text>
          <Text>
            Website: <a
              rel="noopener noreferrer"
              href="https://eqdprotocol.finance"
              target="_blank"
            >https://eqdprotocol.finance</a>
          </Text>
          <Text>
            Twitter: <a
              href="https://twitter.com/EQD_finance"
              target="_blank"
              rel="noopener noreferrer"
            >https://twitter.com/EQD_finance</a>
          </Text>
          <Text>
            Telegram: <a
              href="#"
              target="_blank"
              rel="noopener noreferrer"
            >https://t.me/EQD_finance</a>
          </Text>
          <Text>
            Github: <a
              href="https://github.com/TrueDollar"
              target="_blank"
              rel="noopener noreferrer"
            >https://github.com/EQDfinance</a>
          </Text>

        </div>
        <div>
          <TitleSub>Basics</TitleSub>
          <ul style={{ marginLeft: 40 }}>
            <TextBasic>Epoch Length: 3,600 Seconds (1 Hour)</TextBasic>
            <TextBasic>Advance incentive: 2 EQD</TextBasic>
            <TextBasic>DAO Lockup: 24 Epochs</TextBasic>
            <TextBasic>LP Lockup: 10 Epochs</TextBasic>
            <TextBasic>Source: CAKE-LP BUSD/EQD pair</TextBasic>
            <TextBasic>Method: <a
              rel="noopener noreferrer"
              href="#"
              target="_blank"
            >CAKE-LP Time
          Weighted Average Price (TWAP)</a></TextBasic>
            <TextBasic>Oracle Minimum: 10,000 BUSD</TextBasic>
            <TextBasic>Period: 1 Epoch (1 Hour)</TextBasic>
          </ul>
        </div>
        <div>
          <TitleSub>Governance</TitleSub>
          <Text>Equilibrium Dollar Protocol allows the community to submit proposals and vote for any changes, either
          removing existing features, adding new features or adjust peripheral functionality. The voting can be done by
          the EQD token holders. At the protocol launch, there will be no admin keys or any way to influence it without
        changes being actioned by a governance process. This made EQD a fully decentralized protocol from unset.</Text>
        </div>
        <div>
          <TitleSub>Our Plan</TitleSub>
          <Text>We will launch EQD at 11:00 AM UTC / 17 January 2021</Text>
          <Text>Our plan is to pre-mine only a small number of tokens (half amount of ESD and DSD premined) and they will be
          placed in lock for DAO rewards and also will be used for the initial liquidity for the BUSD/EQD paid. These
        tokens are locked up hence there are no hidden supplies!</Text>
        </div>
        <div>
          <TitleSub>Why EQD?</TitleSub>
          <Text>We believe in the future of algorithm stablecoins like $ESD, $DSD. We also see the problem that long epoch
          time can lead to unstable value of the tokens. This is our experiment to explorer whether fast epoch time can
        adjust the price better or not. So please DYOR before investing in our project.</Text>
        </div>
      </div>
    </div>
  </Container>
}

const Container = styled.div`
  padding: 1% 15px;
`

const Title = styled.p`
  margin-top: 10px;
  font-size: 52px;
  text-align: center;
  font-weight: bold;
  line-height: 1;
`

const TitleSub = styled.h1`
  margin-top: 24px;
  font-size: 21px;
  font-weight: bold;
`

const Text = styled.p`
  margin-top: 24px;
  font-size: 21px;
  line-height: 1.3;
`

const TextSub = styled.p`
  text-align: center;
  font-size: 14px;
  margin-top: 10px;
`

const TextBasic = styled.li`
  margin-top: 10px;
  font-size: 21px;
`

const Logo = styled.img`
  width: auto;
  height: auto;
  background-color: white;
  margin: 24px auto 0 auto;
  display: block;
  @media (max-width: 991px) {
      max - width: 300px;
  }
`

export default About;