import React from 'react';
import { LinkBase, useTheme } from '@aragon/ui';
import ChangeModeButton from "./SwitchTheme";

type FooterProps = {
  updateTheme: Function,
  theme: string,
  hasWeb3: boolean,
}

function Footer({ updateTheme, theme, hasWeb3 }: FooterProps) {
  const currentTheme = useTheme();

  return (

    <div className="navbar navbar-expand-sm navbar-dark bg-dark">
      <div className="container text-white" style={{ paddingBottom: '1%' }}>
        <div>
          <FooterLink icon={<img style={{ width: '50%' }} src='./image/github.png' />} href={"https://github.com/equilibriumdollarprotocol"} />
          <FooterLink icon={<img style={{ width: '50%' }} src='./image/twitter.png' />} href={"https://twitter.com/EQD_finance?s=20"} />
          <FooterLink icon={<img style={{ width: '50%' }} src='./image/medium.png' />} href={"#https://medium.com/@eqdfinance"} />
          <FooterLink icon={<img style={{ width: '50%' }} src='./image/telegram.png' />} href={"https://t.me/eqd_finance"} />
        </div>
        <div>
          <span className="float-right"> &nbsp;&nbsp;made with <span role="img" aria-labelledby="heartbreak"></span> by the &#123;eqd&#125;.</span>
        </div>
      </div>
    </div>
  );
}

type FooterLinkProp = {
  icon: any,
  href: string,
}

function FooterLink({
  icon, href,
}: FooterLinkProp) {
  return (
    <LinkBase href={href} style={{ marginLeft: '8px', marginRight: '8px' }}>
      <span style={{ fontSize: 26 }}>{icon}</span>
    </LinkBase>
  );
}

export default Footer;
