import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import BigNumber from 'bignumber.js';
import {
  getBalanceBonded,
  getBalanceOfStaged, getFluidUntil,
  getStatusOf, getTokenAllowance,
  getTokenBalance, getTokenTotalSupply, getTotalBonded, loadFluidStatusDao,
} from '../../utils/infura';
import { EQD, EQDS } from "../../constants/tokens";
import { DAO_EXIT_LOCKUP_EPOCHS } from "../../constants/values";
import { toTokenUnitsBN } from '../../utils/number';

import AccountPageHeader from "./Header";
import WithdrawDeposit from "./WithdrawDeposit";
import BondUnbond from "./BondUnbond";
import IconHeader from "../common/IconHeader";
import { getPoolAddress } from "../../utils/pool";
import { DollarPool4 } from "../../constants/contracts";
import Invest from "./Invest";

function Wallet({ user }: { user: string }) {
  const { override } = useParams();
  if (override) {
    user = override;
  }

  const [userEQDBalance, setUserEQDBalance] = useState(new BigNumber(0));
  const [userEQDAllowance, setUserEQDAllowance] = useState(new BigNumber(0));
  const [userEQDSBalance, setUserEQDSBalance] = useState(new BigNumber(0));
  const [totalEQDSSupply, setTotalEQDSSupply] = useState(new BigNumber(0));
  const [userStagedBalance, setUserStagedBalance] = useState(new BigNumber(0));
  const [userBondedBalance, setUserBondedBalance] = useState(new BigNumber(0));
  const [userStatus, setUserStatus] = useState(0);
  const [userStatusUnlocked, setUserStatusUnlocked] = useState(0);
  const [lockup, setLockup] = useState(0);
  const [totalSupply, setTotalSupply] = useState(new BigNumber(0));
  const [totalBonded, setTotalBonded] = useState(new BigNumber(0));
  const [fluidStatus, setFluidStatus] = useState({
    lastUnbond: undefined, lastBond: undefined, fluidEpoch: undefined
  });

  //Update User balances
  useEffect(() => {
    let isCancelledApr = false;
    async function updateAPR() {
      const [
        totalSupplyStr,
        totalBondedStr,
      ] = await Promise.all([
        getTokenTotalSupply(EQD.addr),
        getTotalBonded(EQDS.addr),
      ]);

      if (!isCancelledApr) {
        setTotalSupply(toTokenUnitsBN(totalSupplyStr, EQD.decimals));
        setTotalBonded(toTokenUnitsBN(totalBondedStr, EQD.decimals));
      }
    }

    updateAPR();

    if (user === '') {
      setUserEQDBalance(new BigNumber(0));
      setUserEQDAllowance(new BigNumber(0));
      setUserEQDSBalance(new BigNumber(0));
      setTotalEQDSSupply(new BigNumber(0));
      setUserStagedBalance(new BigNumber(0));
      setUserBondedBalance(new BigNumber(0));
      setUserStatus(0);
      return;
    }
    let isCancelledUser = false;

    async function updateUserInfo() {
      const [
        EQDBalance,
        EQDAllowance,
        EQDsBalance,
        EQDsSupply,
        stagedBalance,
        bondedBalance,
        status,
        poolAddress,
        fluidUntilStr,
        totalSupplyStr,
        totalBondedStr,
        fluidStatusStr
      ] = await Promise.all([
        getTokenBalance(EQD.addr, user),
        getTokenAllowance(EQD.addr, user, EQDS.addr),
        getTokenBalance(EQDS.addr, user),
        getTokenTotalSupply(EQDS.addr),
        getBalanceOfStaged(EQDS.addr, user),
        getBalanceBonded(EQDS.addr, user),
        getStatusOf(EQDS.addr, user),
        getPoolAddress(),

        getFluidUntil(EQDS.addr, user),

        getTokenTotalSupply(EQD.addr),
        getTotalBonded(EQDS.addr),
        loadFluidStatusDao(EQDS.addr, user),
      ]);

      const userEQDBalance = toTokenUnitsBN(EQDBalance, EQD.decimals);
      const userEQDSBalance = toTokenUnitsBN(EQDsBalance, EQDS.decimals);
      const totalEQDSSupply = toTokenUnitsBN(EQDsSupply, EQDS.decimals);
      const userStagedBalance = toTokenUnitsBN(stagedBalance, EQDS.decimals);
      const userBondedBalance = toTokenUnitsBN(bondedBalance, EQDS.decimals);
      const userStatus = parseInt(status, 10);
      const fluidUntil = parseInt(fluidUntilStr, 10);

      if (!isCancelledUser) {
        setUserEQDBalance(new BigNumber(userEQDBalance));
        setUserEQDAllowance(new BigNumber(EQDAllowance));
        setUserEQDSBalance(new BigNumber(userEQDSBalance));
        setTotalEQDSSupply(new BigNumber(totalEQDSSupply));
        setUserStagedBalance(new BigNumber(userStagedBalance));
        setUserBondedBalance(new BigNumber(userBondedBalance));
        setUserStatus(userStatus);
        setUserStatusUnlocked(fluidUntil)
        setLockup(poolAddress === DollarPool4 ? DAO_EXIT_LOCKUP_EPOCHS : 1);
        setTotalSupply(toTokenUnitsBN(totalSupplyStr, EQD.decimals));
        setTotalBonded(toTokenUnitsBN(totalBondedStr, EQD.decimals));
        setFluidStatus(fluidStatusStr);
      }
    }
    updateUserInfo();
    const updateUser = setInterval(updateUserInfo, 15000);
    const apr = setInterval(updateAPR, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelledUser = true;
      isCancelledApr = true;
      clearInterval(updateUser);
      clearInterval(apr);
    };
  }, [user]);

  return (
    <>

      <div style={{ textAlign: 'center' }}>

        <IconHeader icon={<div className="modal" style={{ display: 'block', position: 'relative' }}><img width='60px' src="./image/daologo.png" /></div>} text="DAO" />

      </div>
      <Invest
        totalSupply={totalSupply}
        totalBonded={totalBonded}
      />

      <AccountPageHeader
        user={user}
        accountEQDBalance={userEQDBalance}
        accountEQDSBalance={userEQDSBalance}
        totalEQDSSupply={totalEQDSSupply}
        accountStagedBalance={userStagedBalance}
        accountBondedBalance={userBondedBalance}
        accountStatus={userStatus}
        unlocked={userStatusUnlocked}
        fluidEpoch={fluidStatus?.fluidEpoch}
      />

      <WithdrawDeposit
        user={user}
        balance={userEQDBalance}
        allowance={userEQDAllowance}
        stagedBalance={userStagedBalance}
        status={userStatus}
      />

      <BondUnbond
        staged={userStagedBalance}
        bonded={userBondedBalance}
        status={userStatus}
        lockup={lockup}
      />
    </>
  );
}

export default Wallet;
