import React, { useEffect, useState } from 'react';
import styled from 'styled-components'

import NextEpoch from "./NextEpoch";
import {
  getEpoch,
  getPoolTotalBonded, getPoolTotalStaged, getTokenBalance,
  getTokenTotalSupply,
  getTotalBonded, getTotalCoupons, getTotalDebt,
  getTotalRedeemable,
  getTotalStaged,
  getTotalSupplyUni
} from "../../utils/infura";
import { EQD, EQDS, UNI, USDC } from "../../constants/tokens";
import BigNumber from "bignumber.js";
import { toTokenUnitsBN } from "../../utils/number";
import TokenDistribution from "./TokenDistribution";
import HistoryReturn from "./HistoryReturn";
import { getPoolAddress } from "../../utils/pool";

function Tool() {
  const [epoch, setEpoch] = useState(0);

  const [totalSupply, setTotalSupply] = useState(new BigNumber(0));
  const [totalBonded, setTotalBonded] = useState(new BigNumber(0));
  const [totalStaged, setTotalStaged] = useState(new BigNumber(0));
  const [totalSupplyUni, setTotalSupplyUni] = useState(new BigNumber(0));
  const [poolTotalBonded, setPoolTotalBonded] = useState(new BigNumber(0));
  const [poolTotalStaged, setPoolTotalStaged] = useState(new BigNumber(0));

  const [totalRedeemable, setTotalRedeemable] = useState(new BigNumber(0));

  const [totalDebt, setTotalDebt] = useState(new BigNumber(0));
  const [totalCoupons, setTotalCoupons] = useState(new BigNumber(0));

  const [pairBalanceEQD, setPairBalanceEQD] = useState(new BigNumber(0));
  const [pairBalanceUSDC, setPairBalanceUSDC] = useState(new BigNumber(0));

  const price = pairBalanceUSDC.dividedBy(pairBalanceEQD);

  useEffect(() => {
    let isCancelled = false;

    async function getTool() {
      const poolAddressStr = await getPoolAddress();

      const [
        epochStr,

        pairBalanceEQDStr,
        pairBalanceUSDCStr,

        totalSupplyStr,
        totalBondedStr,
        totalStagedStr,
        totalSupplyUniStr,
        poolTotalBondedStr,
        poolTotalStagedStr,
        totalRedeemableStr,
        totalDebtStr,
        totalCouponsStr
      ] = await Promise.all([
        getEpoch(EQDS.addr),

        getTokenBalance(EQD.addr, UNI.addr),
        getTokenBalance(USDC.addr, UNI.addr),

        getTokenTotalSupply(EQD.addr),
        getTotalBonded(EQDS.addr),
        getTotalStaged(EQDS.addr),
        getTotalSupplyUni(),
        getPoolTotalBonded(poolAddressStr),
        getPoolTotalStaged(poolAddressStr),

        getTotalRedeemable(EQDS.addr),
        getTotalDebt(EQDS.addr),
        getTotalCoupons(EQDS.addr),
      ]);

      if (!isCancelled) {
        setEpoch(parseInt(epochStr, 10));

        setPairBalanceEQD(toTokenUnitsBN(pairBalanceEQDStr, EQD.decimals));
        setPairBalanceUSDC(toTokenUnitsBN(pairBalanceUSDCStr, USDC.decimals));

        setTotalSupply(toTokenUnitsBN(totalSupplyStr, EQD.decimals));
        setTotalBonded(toTokenUnitsBN(totalBondedStr, EQD.decimals));
        setTotalStaged(toTokenUnitsBN(totalStagedStr, EQD.decimals));
        setTotalRedeemable(toTokenUnitsBN(totalRedeemableStr, EQD.decimals));
        setTotalSupplyUni(toTokenUnitsBN(totalSupplyUniStr, EQD.decimals));
        setPoolTotalBonded(toTokenUnitsBN(poolTotalBondedStr, EQD.decimals));
        setPoolTotalStaged(toTokenUnitsBN(poolTotalStagedStr, EQD.decimals));
        setTotalDebt(toTokenUnitsBN(totalDebtStr, EQD.decimals));
        setTotalCoupons(toTokenUnitsBN(totalCouponsStr, EQD.decimals));

      }
    }

    getTool();
    const id = setInterval(getTool, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, []);

  const increaseBy = (totalSupply.toNumber() * 4) / 100;
  const daoBonding = (((totalSupply.toNumber() * 4) / 100) * 60) / 100;
  const lpBonding = (((totalSupply.toNumber() * 4) / 100) * 40) / 100;

  return <Container>

    <div style={{ marginBottom: '2%', display: 'block', position: 'relative', background: '#ffffff' }}>
      <h2 style={{ padding: '2%' }} className="bg-warning">Tools</h2>
      <div className="bs-component" style={{ padding: '2%', color: '#000000' }}>
        <Header>
          Next Epoch:
      <p>$EQD Supply will increase by {increaseBy.toFixed(2)} EQD </p>
          <p>{daoBonding.toFixed(2)} EQD for DAO Bonding and {lpBonding.toFixed(2)} EQD for LP Bonding</p>
        </Header>
        <ContainerItem>
          <NextEpoch />
          <div>
            <Title>Spot Price</Title>
            <p>${price.toNumber().toFixed(2)} BUSD</p>
          </div>
          <div>
            <Title>TWAP Price</Title>
            <p>$1.22 BUSD - Bootstrap</p>
          </div>
          <div>
            <Title>Epoch</Title>
            <p>{epoch}</p>
          </div>
        </ContainerItem>
        <ContainerItem>
          <div style={{ flexBasis: '48%', maxWidth: 300 }}>
            <Title>Token Supply</Title>
            <ContainerFlex>
              <div>
                <p>Total Token:</p>
                <p>Coupons:</p>
                <p>Total Supply:</p>
                <p>Debt:</p>
              </div>
              <div className="text-right">
                <p>{(totalSupply.toNumber() - totalCoupons.toNumber()).toFixed(2)}</p>
                <p>{totalCoupons.toNumber().toFixed(2)}</p>
                <p>{totalSupply.toNumber().toFixed(2)}</p>
                <p>{totalDebt.toNumber().toFixed(2)}</p>
              </div>
            </ContainerFlex>
          </div>
          <div style={{ flexBasis: '48%', maxWidth: 300 }}>
            <TokenDistribution
              totalRedeemable={totalRedeemable}
              totalStaged={totalStaged}
              totalBonded={totalBonded}
              totalSupplyUni={totalSupplyUni}
              poolTotalStaged={poolTotalStaged}
              poolTotalBonded={poolTotalBonded}
            />
          </div>
        </ContainerItem>
        <HistoryReturn
          totalSupply={totalSupply}
          totalBonded={totalBonded}
          EQDLPBonded={pairBalanceEQD}
        />

      </div>
    </div>
  </Container>
}

const Header = styled.div`
  text-align: center;
`

const Container = styled.div`
  padding: 1% 15px 1% 15px;
  max-width: 800px;
  margin: 20px auto 0 auto;
`

const ContainerItem = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 20px;
  @media (max-width: 522px) {
    display: block;
  }
`

const ContainerFlex = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
`

const Title = styled.h2`
  font-weight: bold;
`

export default Tool;