import React, { Fragment } from 'react';
import BigNumber from 'bignumber.js';
import { Button } from '@aragon/ui';
import styled from 'styled-components'

import { BalanceBlock } from '../common/index';
import TextBlock from "../common/TextBlock";
import { ownership } from "../../utils/number";
import { PANCAKE_TRADE } from "../../constants/contracts";

type AccountPageHeaderProps = {
  accountEQDBalance: BigNumber,
  accountEQDSBalance: BigNumber,
  totalEQDSSupply: BigNumber,
  accountStagedBalance: BigNumber,
  accountBondedBalance: BigNumber,
  accountStatus: number,
  unlocked: number,
  fluidEpoch: any,
  user: string
};

const STATUS_MAP = ["Unlocked", "Locked", "Locked"];

function status(accountStatus) {
  return STATUS_MAP[accountStatus]
}

const AccountPageHeader = ({
  accountEQDBalance,
  accountEQDSBalance,
  totalEQDSSupply,
  accountStagedBalance,
  accountBondedBalance,
  accountStatus,
  unlocked,
  fluidEpoch,
  user
}: AccountPageHeaderProps) => {

  return (
    <div style={{ margin: '1%' }}>
      <h3>
        <div className='row' style={{ padding: '3%', textAlign: 'center' }}>
          <div className='col-md-2' style={{ paddingBottom: '2%' }}>
            <BalanceBlock asset="Balance" balance={accountEQDBalance} suffix={" EQD"} />
            <Button
              label="Buy EQD"
              icon={<i className="fas fa-exchange-alt" />}
              onClick={() => window.open(PANCAKE_TRADE, "_blank")}
            />
          </div>
          <div className='col-md-2' style={{ paddingBottom: '2%' }}>

            <BalanceBlock asset="Staged" balance={accountStagedBalance} suffix={" EQD"} />
          </div>
          <div className='col-md-2' style={{ paddingBottom: '2%' }}>

            <BalanceBlock asset="Bonded" balance={accountBondedBalance} suffix={" EQD"} />
          </div>
          <div className='col-md-2' style={{ paddingBottom: '2%' }}>

            <BalanceBlock asset="DAO Ownership" balance={ownership(accountEQDSBalance, totalEQDSSupply)} suffix={"%"} />
          </div>

          <div className='col-md-4' style={{ paddingBottom: '2%' }}>

            <TextBlock label="Status" text={status(accountStatus)} />
            {
              user !== '' && (
                <Fragment>
                  <p>{
                    isNaN(fluidEpoch)
                      ? 'You did not bonded or unbonded before.'
                      : `You last bonded or unbonded at epoch ${fluidEpoch}.`
                  } </p>
                  {
                    accountStatus !== 0 && (
                      <p>Unlocked at epoch {fluidEpoch + 24}.</p>
                    )
                  }
                </Fragment>
              )
            }
          </div>
        </div>
      </h3>
    </div>
  );
}

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 2%;
  justify-content: space-between;
  @media (max-width: 522px) {
    display: block;
  }
`


export default AccountPageHeader;
