import React from 'react';
import BigNumber from "bignumber.js";
import styled from 'styled-components'

import { formatNumber } from "../../utils/number";

type TotalSupplyProps = {
  totalSupply: BigNumber,
};

const TotalSupply = ({ totalSupply }: TotalSupplyProps) => {

  return (
    <div>
      <div style={{ fontSize: 16, padding: 3 }}>Total Supply <Icon src="./images/bscscan.jpg"
        onClick={() => window.open('https://bscscan.com/token/0x5Aae642C7ceA19A027590ea42f2366f64377c06F', '_blank')}
      /></div>
      <div style={{
        fontSize: 24,
        padding: 3,
        fontWeight: 400,
        lineHeight: 1.5,
        fontFamily: 'DOS, monospace'
      }}>{formatNumber((totalSupply.toNumber()).toFixed(2))}
      </div>
      <div style={{ fontSize: 16, padding: 3 }}>Status: <strong>EXPANSION (+{((totalSupply.toNumber() * 4) / 100).toFixed()} next epoch)</strong></div>
    </div>
  );
};

const Icon = styled.img`
  border: 1px solid #ffffff;
  border-radius: 10px;
  background-color: #ffffff;
  width: 20px;
  cursor: pointer;
`

export default TotalSupply;