import React, { useState } from 'react';
import {
  Box, Button, IconCirclePlus, IconCircleMinus, IconCaution
} from '@aragon/ui';
import BigNumber from 'bignumber.js';
import {
  BalanceBlock, MaxButton,
} from '../common/index';
import { bond, unbondUnderlying } from '../../utils/web3';
import { isPos, toBaseUnitBN } from '../../utils/number';
import { EQD, EQDS } from "../../constants/tokens";
import BigNumberInput from "../common/BigNumberInput";
import TextBlock from "../common/TextBlock";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

type BondUnbondProps = {
  staged: BigNumber,
  bonded: BigNumber,
  status: number,
  lockup: number,
};

function BondUnbond({
  staged, bonded, status, lockup
}: BondUnbondProps) {
  const [bondAmount, setBondAmount] = useState(new BigNumber(0));
  const [unbondAmount, setUnbondAmount] = useState(new BigNumber(0));

  return (
    <div className="modal" style={{ marginBottom: '2%', display: 'block', position: 'relative', color: '#000000' }}  >
      <h2 style={{ padding: '2%' }} id="nav-breadcrumbs" className="bg-warning">BOND</h2>
      <div style={{ padding: '2%', background: '#ffffff' }}>
        <div className='row'>
          <div className='col-md-2'>

            <BalanceBlock asset="Bonded" balance={bonded} suffix={"EQD"} />
          </div>

          <div className='col-md-2' >
            <TextBlock label="Exit Lockup" text={lockup === 0 ? "" : lockup === 1 ? "1 epoch" : `${lockup} epochs`} />
          </div>

          <div className='col-md-4'>
            <div style={{ display: 'flex' }}>
              <div style={{ width: '60%', minWidth: '6em' }}>
                <>
                  <BigNumberInput
                    adornment="EQD"
                    value={bondAmount}
                    setter={setBondAmount}
                  />
                  <MaxButton
                    onClick={() => {
                      setBondAmount(staged);
                    }}
                  />
                </>
              </div>
              <div style={{ width: '40%', minWidth: '7em' }}>
                {
                  (status === 2 || !isPos(bondAmount) || bondAmount.isGreaterThan(staged))
                    ? <OverlayTrigger
                      placement="bottom"
                      overlay={
                        <Tooltip id="tooltip">
                          Make sure the value &gt; 0
                      </Tooltip>
                      }
                    >
                      <div style={{ display: 'inline-block', cursor: 'not-allowed' }}>
                        <Button
                          style={{ pointerEvents: 'none' }}
                          wide
                          icon={status === 0 ? <IconCirclePlus /> : <IconCaution />}
                          label="Bond"
                          disabled={status === 2 || !isPos(bondAmount) || bondAmount.isGreaterThan(staged)}
                        />
                      </div>
                    </OverlayTrigger>
                    : <Button
                      wide
                      icon={status === 0 ? <IconCirclePlus /> : <IconCaution />}
                      label="Bond"
                      onClick={() => {
                        bond(
                          EQDS.addr,
                          toBaseUnitBN(bondAmount, EQD.decimals),
                        );
                      }}
                    />
                }
              </div>
            </div>
          </div>

          <div className='col-md-4'>
            <div style={{ display: 'flex' }}>
              <div style={{ width: '60%', minWidth: '6em' }}>
                <>
                  <BigNumberInput
                    adornment="EQD"
                    value={unbondAmount}
                    setter={setUnbondAmount}
                  />
                  <MaxButton
                    onClick={() => {
                      setUnbondAmount(bonded);
                    }}
                  />
                </>
              </div>
              <div style={{ width: '40%', minWidth: '7em' }}>
                {
                  (status === 2 || !isPos(unbondAmount) || unbondAmount.isGreaterThan(bonded))
                    ? <OverlayTrigger
                      placement="bottom"
                      overlay={
                        <Tooltip id="tooltip">
                          Make sure the value &gt; 0
                      </Tooltip>
                      }
                    >
                      <div style={{ display: 'inline-block', cursor: 'not-allowed' }}>
                        <Button
                          style={{ pointerEvents: 'none' }}
                          wide
                          icon={status === 0 ? <IconCircleMinus /> : <IconCaution />}
                          label="Unbond"
                          disabled={status === 2 || !isPos(unbondAmount) || unbondAmount.isGreaterThan(bonded)}
                        />
                      </div>
                    </OverlayTrigger>
                    : <Button
                      wide
                      icon={status === 0 ? <IconCircleMinus /> : <IconCaution />}
                      label="Unbond"
                      onClick={() => {
                        unbondUnderlying(
                          EQDS.addr,
                          toBaseUnitBN(unbondAmount, EQD.decimals),
                        );
                      }}
                    />
                }
              </div>

            </div>
          </div>
        </div>

        <div style={{ width: '100%', paddingTop: '2%', textAlign: 'center' }}>
          <span style={{ opacity: 0.5 }}> Bonding events will restart the lockup timer.</span>
        </div>
      </div>

    </div>
  );
}

export default BondUnbond;
