import React from 'react';
import BigNumber from 'bignumber.js';

import { BalanceBlock, AddressBlock } from '../common/index';

type TradePageHeaderProps = {
  pairBalanceEQD: BigNumber,
  pairBalanceUSDC: BigNumber,
  uniswapPair: string,
};

const TradePageHeader = ({
  pairBalanceEQD, pairBalanceUSDC, uniswapPair,
}: TradePageHeaderProps) => {
  const price = pairBalanceUSDC.dividedBy(pairBalanceEQD);

  return (
    <div style={{ padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="EQD Price" balance={price} suffix={"BUSD"} />
      </div>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="EQD Liquidity" balance={pairBalanceEQD} suffix={"EQD"} />
      </div>
      <div style={{ flexBasis: '25%' }}>
        <BalanceBlock asset="BUSD Liquidity" balance={pairBalanceUSDC} suffix={"BUSD"} />
      </div>

    </div>
  );
}


export default TradePageHeader;
