import React from 'react';

import {
  Header,
} from '@aragon/ui';

type IconHeaderProps = {
  icon: any,
  text: string
}

function IconHeader({ icon, text }: IconHeaderProps) {
  return (
    <>
      <div style={{ padding: '1%', alignItems: 'center' }}>
        <div style={{ fontSize: 48 }}>
          {icon}
        </div>
        <div>
          <label style={{ fontSize: 25, color: '#ffffff' }}><br />{text}</label>
        </div>
      </div>
    </>
  );
}

export default IconHeader;
