import React from 'react';
import BigNumber from "bignumber.js";
import { Box } from '@aragon/ui';
import styled from 'styled-components'

type InvestProps = {
  totalSupply: BigNumber,
  totalBonded: BigNumber,
};

const Invest = ({ totalSupply, totalBonded }: InvestProps) => {
  const dao = (((((totalSupply.toNumber() * 4) / 100) * 60) / 100 + totalBonded.toNumber()) / totalBonded.toNumber());

  return (
    <Container>
      <ContainerItem style={{ flexBasis: '30%', marginRight: '2%' }}>
        <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
          <ul className="list-group" style={{ background: '#000084' }}>
            <li className="d-flex justify-content-between align-items-center" style={{ padding: '5%', height: '220px' }}>
              <div>
                <div style={{ fontSize: 30, padding: 3 }}>APR</div>
                <div className="mt-4" style={{ fontSize: 16, padding: 3 }}>DAO hourly: <label style={{ float: 'right' }}>{((dao - 1) * 100).toFixed(2)}%</label>

                </div>
                <div style={{ fontSize: 16, padding: 3 }}>DAO daily: <label style={{ float: 'right' }}>{((dao - 1) * 24 * 100).toFixed(2)}%</label>

                </div>
                <div style={{ fontSize: 16, padding: 3 }}>DAO weekly: <label style={{ float: 'right' }}>{((dao - 1) * 168 * 100).toFixed(2)}%</label>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </ContainerItem>
      <ContainerItem style={{ flexBasis: '68%' }}>
        <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
          <ul className="list-group" style={{ background: '#000084' }}>
            <li className="d-flex justify-content-between align-items-center" style={{ height: '100px' }}>
              <label style={{ fontSize: 20 }}><strong>Guide for DAO bonding</strong></label>
            </li>
            <li>
              <p >Step 1: You need to have EQD in your wallet.</p>
            </li>
            <li>
              <p className="mt-2">Step 2: Approve your EQD to unlock Staging (skip it if you've done it)</p>
            </li>
            <li>
              <p className="mt-2">Step 3: Stage your EQD into DAO</p>
            </li>
            <li>
              <p className="mt-2">Step 4: Bond your EQD into DAO</p>
            </li>
            <li>
              <p className="mt-2">Step 5: Unbond and wait 24 hours to claim your rewards</p>
            </li>
          </ul>
        </div>
      </ContainerItem>
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 1%;
  @media (max-width: 522px) {
    display: block;
  }
`

const ContainerItem = styled.div`
  @media (max-width: 522px) {
    margin: 2% !important;
  }
`

export default Invest;