import React, { useState } from 'react';
import {
  Box, Button, IconArrowUp, IconCirclePlus
} from '@aragon/ui';
import BigNumber from 'bignumber.js';
import { BalanceBlock, MaxButton, PriceSection } from '../common/index';
import { approve, providePool } from '../../utils/web3';
import { isPos, toBaseUnitBN, toTokenUnitsBN } from '../../utils/number';
import { EQD, USDC } from "../../constants/tokens";
import { MAX_UINT256 } from "../../constants/values";
import BigNumberInput from "../common/BigNumberInput";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

type ProvideProps = {
  poolAddress: string,
  user: string,
  rewarded: BigNumber,
  pairBalanceEQD: BigNumber,
  pairBalanceUSDC: BigNumber,
  userUSDCBalance: BigNumber,
  userUSDCAllowance: BigNumber,
  status: number,
};

function Provide({
  poolAddress,
  user,
  rewarded,
  pairBalanceEQD,
  pairBalanceUSDC,
  userUSDCBalance,
  userUSDCAllowance,
  status
}: ProvideProps) {
  const [provideAmount, setProvideAmount] = useState(new BigNumber(0));
  const [usdcAmount, setUsdcAmount] = useState(new BigNumber(0));

  const USDCToEQDRatio = pairBalanceUSDC.isZero() ? new BigNumber(1) : pairBalanceUSDC.div(pairBalanceEQD);

  const onChangeAmountEQD = (amountEQD) => {
    if (!amountEQD) {
      setProvideAmount(new BigNumber(0));
      setUsdcAmount(new BigNumber(0));
      return;
    }

    const amountEQDBN = new BigNumber(amountEQD)
    setProvideAmount(amountEQDBN);

    const amountEQDBU = toBaseUnitBN(amountEQDBN, EQD.decimals);
    const newAmountUSDC = toTokenUnitsBN(amountEQDBU.multipliedBy(USDCToEQDRatio).integerValue(BigNumber.ROUND_FLOOR), EQD.decimals);
    setUsdcAmount(newAmountUSDC);
  };

  const handleMaxUSDC = () => {
    const newAmountEQD = userUSDCBalance.dividedBy(USDCToEQDRatio).toFixed(0, 1);
    onChangeAmountEQD(newAmountEQD)
  }

  return (
    <div style={{ marginBottom: '2%', display: 'block', position: 'relative', background: '#ffffff' }}>
      <h2 style={{ padding: '2%' }} className="bg-warning">Provide</h2>
      <div className="bs-component" style={{ padding: '2%', color: '#000000' }}>
        {userUSDCAllowance.comparedTo(MAX_UINT256.dividedBy(2)) > 0 ?
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            {/* total rewarded */}
            <div style={{ flexBasis: '32%' }}>
              <BalanceBlock asset="Rewarded" balance={rewarded} suffix={"EQD"} />
            </div>
            <div style={{ flexBasis: '33%' }}>
              <BalanceBlock asset="BUSD Balance" balance={userUSDCBalance} suffix={"BUSD"} />
            </div>
            <div style={{ flexBasis: '2%' }} />
            {/* Provide liquidity using Pool rewards */}
            <div style={{ flexBasis: '33%', paddingTop: '2%' }}>
              <div style={{ display: 'flex' }}>
                <div style={{ width: '60%', minWidth: '6em' }}>
                  <>
                    <BigNumberInput
                      adornment="EQD"
                      value={provideAmount}
                      setter={onChangeAmountEQD}
                      disabled={status === 1}
                    />
                    <PriceSection label="Requires " amt={usdcAmount} symbol=" USDC" />
                    <div className="d-flex">
                      <MaxButton
                        onClick={() => {
                          onChangeAmountEQD(rewarded);
                        }}
                        title="Max EQD"
                      />
                      <div className="ml-4">
                        <MaxButton
                          onClick={handleMaxUSDC}
                          title="Max BUSD"
                        />
                      </div>
                    </div>
                  </>
                </div>
                <div style={{ width: '40%', minWidth: '6em' }}>
                  {
                    (poolAddress === '' || status !== 0 || !isPos(provideAmount) || usdcAmount.isGreaterThan(userUSDCBalance))
                      ? <OverlayTrigger
                        placement="bottom"
                        overlay={
                          <Tooltip id="tooltip">
                            Make sure the value &gt; 0 and your status is Unlocked.
                        </Tooltip>
                        }
                      >
                        <div style={{ display: 'inline-block', cursor: 'not-allowed' }}>
                          <Button
                            style={{ pointerEvents: 'none' }}
                            wide
                            icon={<IconArrowUp />}
                            label="Provide"
                            disabled={poolAddress === '' || status !== 0 || !isPos(provideAmount) || usdcAmount.isGreaterThan(userUSDCBalance)}
                          />
                        </div>
                      </OverlayTrigger>
                      : <Button
                        wide
                        icon={<IconArrowUp />}
                        label="Provide"
                        onClick={() => {
                          providePool(
                            poolAddress,
                            toBaseUnitBN(provideAmount, EQD.decimals),
                            (hash) => setProvideAmount(new BigNumber(0))
                          );
                        }}
                      />
                  }
                </div>
              </div>
            </div>
          </div>
          :
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            {/* total rewarded */}
            <div style={{ flexBasis: '32%' }}>
              <BalanceBlock asset="Rewarded" balance={rewarded} suffix={"EQD"} />
            </div>
            <div style={{ flexBasis: '33%' }}>
              <BalanceBlock asset="USDC Balance" balance={userUSDCBalance} suffix={"BUSD"} />
            </div>
            <div style={{ flexBasis: '2%' }} />
            {/* Approve Pool to spend USDC */}
            <div style={{ flexBasis: '33%', paddingTop: '2%' }}>
              <Button
                wide
                icon={<IconCirclePlus />}
                label="Approve"
                onClick={() => {
                  approve(USDC.addr, poolAddress);
                }}
                disabled={poolAddress === '' || user === ''}
              />
            </div>
          </div>
        }
        <div style={{ width: '100%', paddingTop: '2%', textAlign: 'center' }}>
          <span style={{ opacity: 0.5 }}> Zapping requires your Bonded balance &gt; 0 and your status as Unlocked. </span>
        </div>
      </div>
    </div>
  );
}

export default Provide;
