import React, { useState, useEffect } from 'react';
import { LinkBase, Box } from '@aragon/ui';

import BigNumber from 'bignumber.js';
import { getTokenBalance } from '../../utils/infura';
import { toTokenUnitsBN } from '../../utils/number';

import TradePageHeader from './Header';
import { EQD, UNI, USDC } from "../../constants/tokens";
import IconHeader from "../common/IconHeader";
import { PANCAKE_INFO, PANCAKE_SUPPLY, PANCAKE_TRADE } from "../../constants/contracts";


function UniswapPool({ user }: { user: string }) {
  const [pairBalanceEQD, setPairBalanceEQD] = useState(new BigNumber(0));
  const [pairBalanceUSDC, setPairBalanceUSDC] = useState(new BigNumber(0));

  useEffect(() => {
    let isCancelled = false;

    async function updateUserInfo() {
      const [
        pairBalanceEQDStr, pairBalanceUSDCStr,
      ] = await Promise.all([
        getTokenBalance(EQD.addr, UNI.addr),
        getTokenBalance(USDC.addr, UNI.addr),
      ]);

      if (!isCancelled) {
        setPairBalanceEQD(toTokenUnitsBN(pairBalanceEQDStr, EQD.decimals));
        setPairBalanceUSDC(toTokenUnitsBN(pairBalanceUSDCStr, USDC.decimals));
      }
    }

    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <>
      <div style={{ textAlign: 'center' }}>
        <IconHeader icon={<div className="modal" style={{ display: 'block', position: 'relative' }}><i className="fas fa-exchange-alt" /></div>} text="Trade" />
      </div>

      <TradePageHeader
        pairBalanceEQD={pairBalanceEQD}
        pairBalanceUSDC={pairBalanceUSDC}
        uniswapPair={UNI.addr}
      />

      <div className="bs-docs-section">

        <div className="row">
          <div className="col-lg-4">
            <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
              <ul className="list-group" style={{ background: '#000084' }}>
                <li className="d-flex justify-content-between align-items-center">
                  <MainButton
                    title="Info"
                    description="View EQD-BUSD pool stats."
                    icon={<i className="fas fa-chart-area" />}
                    href={PANCAKE_INFO}
                  />
                </li>
              </ul>
            </div>
          </div>

          <div className="col-lg-4">
            <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
              <ul className="list-group" style={{ background: '#000084' }}>
                <li className="d-flex justify-content-between align-items-center">
                  <MainButton
                    title="Trade"
                    description="Trade EQD tokens."
                    icon={<i className="fas fa-exchange-alt" />}
                    href={PANCAKE_TRADE}
                  />
                </li>
              </ul>
            </div>
          </div>

          <div className="col-lg-4">
            <div className="bs-component modal" style={{ display: 'block', position: 'relative' }}>
              <ul className="list-group" style={{ background: '#000084' }}>
                <li className="d-flex justify-content-between align-items-center">
                  <MainButton
                    title="Supply"
                    description="Supply and redeem liquidity."
                    icon={<i className="fas fa-water" />}
                    href={PANCAKE_SUPPLY}
                  />
                </li>
              </ul>
            </div>
          </div>

        </div>

      </div>

    </>
  );
}

type MainButtonProps = {
  title: string,
  description: string,
  icon: any,
  href: string
}

function MainButton({
  title, description, icon, href,
}: MainButtonProps) {
  return (
    <LinkBase href={href} style={{ width: '100%' }}>
      <div style={{ padding: 15 }}>
        <div style={{ padding: 10, fontSize: 18 }}>
          {title}
        </div>
        <span style={{ fontSize: 48 }}>
          {icon}
        </span>
        {/*<img alt="icon" style={{ padding: 10, height: 64 }} src={iconUrl} />*/}
        <div style={{ paddingTop: 5 }}>
          {' '}
          {description}
          {' '}
        </div>

      </div>
    </LinkBase>
  );
}

export default UniswapPool;
