import React from 'react';
import BigNumber from 'bignumber.js';
import styled from 'styled-components'
import { Button } from '@aragon/ui';

import { BalanceBlock, AddressBlock } from '../common/index';
import { PANCAKE_INFO, PANCAKE_SUPPLY, PANCAKE_TRADE, DEXTOOL } from "../../constants/contracts";

type TradeProps = {
  pairBalanceEQD: BigNumber,
  pairBalanceUSDC: BigNumber,
  uniswapPair: string,
};

const Trade = ({ pairBalanceEQD, pairBalanceUSDC, uniswapPair }: TradeProps) => {
  const price = pairBalanceUSDC.dividedBy(pairBalanceEQD);

  return (
    <>
      <div style={{ fontSize: 18, alignItems: 'center', color: '#ffffff', textAlign: 'center', marginBottom: '2%' }}>
        <div style={{ fontSize: 48 }}>
          <div className="modal" style={{ display: 'block', position: 'relative' }}>
            <img width='60px' src="./image/trade.png" />
          </div>

        </div>
        <div className="center">
          <br />Trade on PancakeSwap
        </div>
      </div>

      <div>
        {/* <Container> */}
        <div className='row'>
          <div className='col-md-4'>
            <div className='bg-component'>
              <div className="card border-primary mb-3">
                <div className="card-header" style={{ background: 'rgb(254 165 0)', color: '#000000', padding: '3%' }}>EQD Price</div>
                <div className="card-body">
                  <p className="card-text text-center" style={{ padding: 3 }}>
                    <BalanceBlock asset="" balance={price} suffix={"BUSD"} />
                    <Button
                      label="Buy EQD"
                      icon={<i className="fas fa-exchange-alt" />}
                      onClick={() => window.open(PANCAKE_TRADE, "_blank")}
                    />
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div className='col-md-4'>
            <div className='bg-component'>
              <div className="card border-primary mb-3">
                <div className="card-header" style={{ background: 'rgb(254 165 0)', color: '#000000', padding: '3%' }}>EQD Liquidity</div>
                <div className="card-body">
                  <p className="card-text text-center" style={{ padding: 3 }}>
                    <BalanceBlock asset="" balance={pairBalanceEQD} suffix={"EQD"} />
                    <Button
                      label="Info"
                      icon={<i className="fas fa-chart-area" />}
                      onClick={() => window.open(PANCAKE_INFO, "_blank")}
                    />
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div className='col-md-4'>
            <div className='bg-component'>
              <div className="card border-primary mb-3">
                <div className="card-header" style={{ background: 'rgb(254 165 0)', color: '#000000', padding: '3%' }}>BUSD Liquidity</div>
                <div className="card-body">
                  <p className="card-text text-center" style={{ padding: 3 }}>
                    <BalanceBlock asset="" balance={pairBalanceUSDC} suffix={"BUSD"} />
                    <Button
                      label="Add"
                      icon={<i className="fas fa-water" />}
                      onClick={() => window.open(PANCAKE_SUPPLY, "_blank")}
                    />
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* <div className='col-md-3'>
            <div className='bg-component'>
              <div className="card border-primary mb-3">
                <div className="card-header" style={{ background: 'rgb(254 165 0)', color: '#000000', padding: '3%' }}>Pancake Contract</div>
                <div className="card-body">
                  <p className="card-text text-center" >
                    <AddressBlock label="" address={uniswapPair} />
                    <Button
                      label="Chart"
                      icon={<i className="fas fa-chart-bar" />}
                      onClick={() => window.open(DEXTOOL, "_blank")}

                    />
                  </p>
                </div>
              </div>
            </div>
          </div> */}

        </div>

        {/* </Container> */}
      </div>


    </>
  );
}

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 3% 3%;
  justify-content: space-between;
  @media (max-width: 522px) {
    display: block;
  }
`

export default Trade;
